html {
  color-scheme: light dark;
  height: 100%;
  font-family: system-ui;
}

body {
  box-sizing: border-box;
  background-color: #f5f5f5;
  min-height: 100vh;
  margin: 0;
  padding: 48px 20px 80px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding-top: 12px;
}

.page {
  display: none;
}

.page.active {
  display: block;
}

h1 {
  color: #333;
  text-align: center;
  margin-bottom: 30px;
}

.card {
  background: #fff;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.card h2 {
  color: #333;
  margin-top: 0;
  margin-bottom: 15px;
}

#systemInfo {
  color: #333;
  line-height: 1.6;
}

.config-list {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  min-height: 100px;
  margin-bottom: 20px;
  display: grid;
}

.config-card {
  cursor: pointer;
  background: #fff;
  border: 2px solid #0000;
  border-radius: 8px;
  padding: 15px;
  transition: transform .2s, box-shadow .2s, border-color .3s;
  box-shadow: 0 2px 4px #0000001a;
}

.config-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #00000026;
}

.config-card.selected {
  background-color: #f1f8e9;
  border-color: #4caf50;
}

.config-card.selected:hover {
  background-color: #e8f5e9;
}

.config-card-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.config-card-header h3 {
  color: #333;
  word-break: break-all;
  margin: 0;
  font-size: 16px;
}

.config-card-actions {
  gap: 8px;
  display: flex;
}

.icon-button {
  cursor: pointer;
  opacity: .7;
  background: none;
  border: none;
  padding: 4px;
  font-size: 18px;
  transition: opacity .2s, transform .2s, background-color .3s;
}

.icon-button:hover {
  opacity: 1;
}

.icon-button.delete-button {
  color: #666;
}

.icon-button.delete-button.delete-pending {
  color: #d32f2f;
  background-color: #ffebee;
  border-radius: 4px;
  transform: scale(1.1);
}

.no-files, .error-message {
  text-align: center;
  color: #666;
  background: #fff;
  border-radius: 8px;
  grid-column: 1 / -1;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.error-message {
  color: #d32f2f;
}

input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 14px;
}

button:hover {
  opacity: .9;
}

pre {
  color: #333;
  background-color: #f8f8f8;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
  overflow-x: auto;
}

.bottom-nav {
  background: #fff;
  justify-content: space-around;
  padding: 10px 0;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 -2px 4px #0000001a;
}

.nav-item {
  cursor: pointer;
  opacity: .6;
  flex-direction: column;
  align-items: center;
  padding: 5px 0;
  transition: opacity .3s;
  display: flex;
}

.nav-item.active {
  opacity: 1;
}

.nav-icon {
  margin-bottom: 4px;
  font-size: 24px;
}

.nav-text {
  color: #666;
  font-size: 12px;
}

.editor-header {
  border-bottom: 1px solid #eee;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 15px;
  display: flex;
}

.editor-header h2 {
  color: #333;
  margin: 0;
  font-size: 18px;
  font-weight: 500;
}

.save-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 8px 20px;
  font-size: 14px;
  transition: background-color .3s;
}

.save-button:hover {
  background-color: #45a049;
}

.editor-textarea {
  resize: none;
  box-sizing: border-box;
  color: #333;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  height: calc(100vh - 200px);
  padding: 12px;
  font-family: monospace;
  font-size: 14px;
  line-height: 1.5;
}

.config-card-status {
  color: #d32f2f;
  text-align: center;
  background-color: #ffebee;
  border-radius: 4px;
  margin-top: 10px;
  padding: 8px;
  font-size: 14px;
  animation: .3s fadeIn;
  display: none;
}

.config-card-status.show {
  display: block;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.top-nav {
  z-index: 100;
  background: #fff;
  align-items: center;
  height: 36px;
  padding: 6px 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 2px 4px #0000001a;
}

.top-nav-content {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
}

.top-nav h1 {
  color: #333;
  margin: 0;
  font-size: 15px;
}

.action-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  font-size: 14px;
  transition: opacity .2s;
  display: flex;
}

.action-button:hover {
  opacity: .9;
}

.action-icon {
  cursor: pointer;
  color: #4caf50;
  justify-content: center;
  align-items: center;
  padding: 4px;
  font-size: 1.2rem;
  transition: transform .2s;
  display: flex;
}

.action-icon:hover {
  transform: scale(1.1);
}

.config-name-input {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 8px;
  font-size: 14px;
}

.action-buttons {
  align-items: center;
  gap: 12px;
  display: flex;
}

#runButton {
  color: #2196f3;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #1a1a1a;
  }

  .card {
    background: #2d2d2d;
  }

  .card h2, #systemInfo {
    color: #fff;
  }

  pre {
    color: #fff;
    background-color: #2d2d2d;
  }

  input[type="text"] {
    color: #fff;
    background-color: #2d2d2d;
    border-color: #444;
  }

  input[type="text"]::placeholder {
    color: #888;
  }

  .config-card {
    background: #2d2d2d;
  }

  .config-card-header h3, .icon-button {
    color: #fff;
  }

  .icon-button.delete-button.delete-pending, .config-card-status {
    color: #ff6b6b;
    background-color: #4a1f1f;
  }

  .bottom-nav {
    background: #2d2d2d;
    box-shadow: 0 -2px 4px #0000004d;
  }

  .nav-text {
    color: #fff;
  }

  .nav-item {
    opacity: .7;
  }

  .nav-item.active {
    opacity: 1;
  }

  .config-card.selected {
    background-color: #1b3a1b;
    border-color: #4caf50;
  }

  .config-card.selected:hover {
    background-color: #1e421e;
  }

  .top-nav {
    background: #2d2d2d;
    box-shadow: 0 2px 4px #0000004d;
  }

  .top-nav h1 {
    color: #fff;
  }

  .config-name-input {
    color: #fff;
    background-color: #2d2d2d;
    border-color: #444;
  }

  .config-name-input::placeholder {
    color: #888;
  }

  .editor-header {
    border-bottom-color: #444;
  }

  .editor-header h2 {
    color: #fff;
  }

  .editor-textarea {
    color: #fff;
    background-color: #2d2d2d;
    border-color: #444;
  }

  .save-button {
    background-color: #2e7d32;
  }

  .save-button:hover {
    background-color: #1b5e20;
  }
}

.status-bar {
  background: #f5f5f5;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 10px 15px;
  box-shadow: 0 1px 3px #0000001a;
}

.status-item {
  align-items: center;
  margin: 5px 0;
  display: flex;
}

.status-label {
  color: #666;
  margin-right: 10px;
  font-weight: bold;
}

.status-value {
  border-radius: 4px;
  padding: 2px 8px;
  font-size: .9em;
}

.status-value.running {
  color: #1e7e34;
  background-color: #e6f4ea;
}

.status-value.stopped {
  color: #d32f2f;
  background-color: #fbe9e7;
}

.toggle-button {
  cursor: pointer;
  z-index: 1000;
  background-color: #4caf50;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  transition: all .3s;
  display: flex;
  position: fixed;
  bottom: 80px;
  right: 20px;
  box-shadow: 0 2px 8px #0003;
}

.toggle-button:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 12px #0000004d;
}

.toggle-button.running {
  background-color: #f44336;
}

.toggle-icon {
  color: #fff;
  font-size: 24px;
}

@media (prefers-color-scheme: dark) {
  .toggle-button {
    background-color: #2e7d32;
  }

  .toggle-button.running {
    background-color: #c62828;
  }

  .status-bar {
    background: #2d2d2d;
    box-shadow: 0 1px 3px #0000004d;
  }

  .status-label {
    color: #bbb;
  }

  .status-value.running {
    color: #4caf50;
    background-color: #1b3a1b;
  }

  .status-value.stopped {
    color: #f44336;
    background-color: #3a1b1b;
  }
}
